.personal-profile img{
  border-radius: 5px;
  box-shadow: 0 0 27px rgba(96,96,96,.34);
  width: 90%;
  height: 380px;
  -o-object-fit: cover;
  object-fit: cover;
  margin-top: 10px;
  position: relative;
  top: 40px;
}
header{
  background-color: #6d56c1;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  position: relative;
  margin-bottom: 40px;
  /* margin-top: -60px; */
}
header:before {
  background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95));
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  content: "";
  z-index: 0;
}
.main-header{
  background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95));
  border-bottom: 0px transparent;
}
header{
  border-bottom: 0px;
}
.navbar{
  background-color: transparent !important;
}
.navbar-nav .nav-item a{
  color: #dedede !important;
  transition: .51s;
  position: relative;
  padding-bottom: 0 !important;
  margin-bottom: 0 !important;
  font-size: 1.2rem;
}
.navbar-nav .nav-item a:after{
  content: "";
  position: absolute;
  width: 7px;
  height: 2px;
  bottom: 10px;
  right: 4px;
  opacity: 0;
  transition: background .3s;
  background: #1e1e1e;
}
.navbar-nav .nav-item a:hover{
  color: #1e1e1e !important;
}
.navbar-nav .nav-item a:hover:after{
 opacity: 1;;
}
.personal-profile .personal-name {
  font-size: 44px;
  line-height: 50px;
  font-weight: 700;
  margin-top: 40px;
  margin-bottom: 10px;
  color: #fff;
}
.personal-designation{
  color:#dedede ;}

.profile-info{
  color: #dedede;
}
.project {
  margin-left: 0;
  margin-bottom: 40px;
  transition: box-shadow .3s;
  margin-right: 0
}
.project:hover {
  box-shadow: 0 1px 31px rgba(0, 0, 0, .09);
  cursor: pointer
}
.project_info{
  border: 1px solid #f1f1f1;
  padding: 50px 50px 40px 30px
}
.project_title{
  font-size: 18px;
  line-height: 30px;
  font-family: Roboto Mono, monospace;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-weight: 700
}
.project_description{
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 45px
}
.project_stack{
  font-family: Roboto Mono, monospace;
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px
}
.tags li {
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(0,0,0,.14);
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 3px;
  margin-bottom: 10px;
}
.project_img img{
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover
}
ul{
  padding: 0;
}
ul li{
  display: inline-block;
}
img{
  max-width: 100%;
}
.menu {
 
  z-index: 100;
  width: 100%;
  height: 70px;
  transition: all .5s
}
.menu li{
  font-size: 14px;
  line-height: 20px;
  font-family: Roboto Mono, monospace;
  text-transform: uppercase;
  font-weight: 700
}
.menu li a{
  color: #fff;
  opacity: 1
}
.menu li  a:hover {
  opacity: .5
}
.mixitup-control-active{
  color: rgba(74,76,179,.95);
  /* background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95)); */
}
.position-relative{
  background-color: #c7c7c7;
}

.profile-social a{
  width: 45px;
  color: #404040;
}
.profile-social a:hover{
  color: rgba(235, 235, 240, 0.95);
}
.profile-social a i{
  font-size: 20px;
  width: 30px;
  height: 30px;
}
@media (max-width: 767.98px){
  .skill{
      margin: 25px;
      border-radius: 10px;
  }
}